import React from "react"
import ParallaxCard from 'react-parallax-card'
import MapHarbor from "./img/Harbor.jpg"
import DominiqueButtiens from "./img/Dominique Buttiens.jpg"
import Falcon from "./img/VikingVillige.jpg"


import "./Maps.css"
const Maps = () =>{
  var style = {}
      {window.screen.width == 360 ?  style = { 
        marginLeft: "20px",margin: '0 auto', width: 200, height: 200 
} :    style = { 
  marginLeft: "20px",margin: '0 auto', width: 500, height: 200 
} }
    return (
      <div className="MapsClass">
      <h1>You can have a deeper look at the Maps now!</h1>
        <ParallaxCard
        label='Harbor Map'
        enableRotate
        enableTransform
        style={style}>
      
        <img src={MapHarbor} style={{ maxWidth: '500px', maxHeight: '100%' }} />
      
        <div /><div /><div />
      
        <img  style={{ top: '67%', left: '75%'  }} />
      
        <p style={{ fontSize: '3rem', fontWeight: 'bold', padding: '3rem' }}>
          
        </p>
      
      </ParallaxCard>
      <br/>
      <h1 style={{paddingTop: "60px"}}>take a look at the Harbor Map</h1>
      <ParallaxCard
      label='Harbor Map'
      enableRotate
      enableTransform
      style={style}>
    
      <img src={DominiqueButtiens} style={{ maxWidth: '500px', maxHeight: '100%' }} />
    
      <div /><div /><div />
    
      <img  style={{ top: '67%', left: '75%'  }} />
    
      <p style={{ fontSize: '3rem', fontWeight: 'bold', padding: '3rem' }}>
        
      </p>
    
    </ParallaxCard>
    <br/>
    <h1 style={{paddingTop: "60px"}}>take a look at the Viking Village Map</h1>
    <ParallaxCard
    label='Viking Villiage Map'
    enableRotate
    enableTransform
    style={style}>
  
    <img src={Falcon} style={{ maxWidth: '500px', maxHeight: '30%' }} />
  
    <div /><div /><div />
  
    <img  style={{ top: '67%', left: '75%'  }} />
  
    <p style={{ fontSize: '3rem', fontWeight: 'bold', padding: '3rem' }}>
      
    </p>
  
  </ParallaxCard>
  <h1 id="goodbye">Thank you for visiting our website</h1>
      </div>
    )

}
export default Maps