import React from "react"
import "./Startsite.css"
import SliderShow2 from "./img/SliderShow2.jpg"
import SliderShow3 from "./img/SliderShow3.jpg"

class Startsite extends React.Component{
 constructor(){
     super()
     this.state = {
         images: [ "https://8images.cgames.de/images/gamestar/226/for-honor_6023107.jpg"
        , SliderShow2
        , SliderShow3
    
    ],
    counter: 0,
    Url: "https://8images.cgames.de/images/gamestar/226/for-honor_6023107.jpg"
  
     }
 
    
setInterval(()=>{
         this.playlist()
     }, 2000 )
    
     

 }
 playlist = () =>{

if(this.state.counter< this.state.images.length){
this.setState(prevState =>  {
    return {
    Url: this.state.images[this.state.counter],
    counter: prevState.counter + 1
    }

})

}else{
    this.setState({
        counter: 0
    })
}
 



 }

render(){
   var  AStyle = {}
      {window.screen.width == 360 ?   AStyle = { 
        alignItems: "center", borderRadius: "6%", border: "dotted", borderColor: "red" , width: "80%", margin: "30px", textAlign: "center"
} :     AStyle = { 
    alignItems: "center", borderRadius: "6%", border: "dotted",borderColor: "red" , width: "70%", margin: "50px", textAlign: "center", marginLeft: "100px"
} }
        return (
            <div  className="HomePage">
            <h1>RESISTANCE</h1>
<p>The Order of Horkos has imposed a regime of terror. But in the most desperate times, a spark of rebellion is all you need to bring back hope... Year 4 Season 3 Resistance is available on September 17th 2020</p> 
            <a href="" id="linki"> <img style={AStyle} id="SliderShow" src={this.state.Url} name="slideri"/></a>
            </div>
        )
    }
    
    
    }
export default Startsite