import React from "react"
import './DLCCards.css'
 function DCards(props){
 
     
     return(
     
        <div className="DLC-card">
        <img alt="..." src={props.ImgUrl}/>
        <h3 className="textDescription">{props.description}</h3>
        <p className="textDescription">Price: {props.price}€</p>
        <p id="Rdate" className="textDescription">Release date: {props.Rdate}</p>
    </div> 

  
     )
 } 


export default DCards