import React, {Fragment , useContext} from "react"
import {NavLink} from "react-router-dom"

import Context from './store/context'
import "./Nav.css";



export function Nav(props) {

  const {state} = useContext(Context)


 return (
        
        <Fragment>

         
        <td 
        onClick={()=> props.HC()} 
        className="NavTableTD"> 
        <button 
        style={props.Appstyle} 
        className="NavBtn">  
        <NavLink 
        className="NavTabeleTD" 
        activeClassName="clicked" 
        to="/Startsite">
        Home Page 
        </NavLink></button>
        </td> 

        <td 
        id="ContentNav"
        onClick={()=> props.HC()} 
        className="NavTableTD"> 
        
        <button 
        style={props.Appstyle} 
        className="NavBtn">  
        
        <NavLink 
        className="NavTabeleTD"  
        activeClassName="clicked" 
        to="/Hero">
        Content 
        <div id="OptionstoDropDown">
          Hero/Maps
        </div>
        </NavLink> 
        </button> 
        
        <div className="drop-down-content">
        
        <button 
        style={props.Appstyle} 
        className="NavBtn">  
        <NavLink 
        className="NavTabeleTD"  
        activeClassName="clicked"  
        to="/Maps">Maps 
        </NavLink>
        </button>
        
        <button 
        style={props.Appstyle} 
        className="NavBtn">   
        <NavLink className="NavTabeleTD"  
        activeClassName="clicked"  
        to="/Hero">Hero
        </NavLink>
        </button>
        </div>
        </td>



        <td 
        onClick={()=> props.HC()} 
        className="NavTableTD">
        <button style={props.Appstyle} 
        className="NavBtn">
        <NavLink  
        className="NavTabeleTD"
        activeClassName="clicked" 
        to="/News"> 
        News
       </NavLink>
       </button>
        </td>
        
        <td 
        onClick={()=> props.HC()} 
        className="NavTableTD"> 
        <button 
        style={props.Appstyle} 
        className="NavBtn">
        <NavLink 
        className="NavTabeleTD" 
        activeClassName="clicked" 
        to="/DLC"> 
        DLC
        </NavLink>
        </button>
        </td>
        
        <td 
        onClick={()=> props.HC()} 
        className="NavTableTD"> 
        
        <button 
        style={props.Appstyle} 
        className="NavBtn">
        
        <NavLink 
        className="NavTabeleTD" 
        activeClassName="clicked" 
        to="/Sign up">
        {state.value}
        </NavLink></button>
        </td>
        </Fragment>
           
         

        
        
    )
   
}
